<template>
  <div class="wrap">
    <div class="in-top-banner">
      <in-banner :detail="isDetail"></in-banner>
    </div>
    <transition name="el-fade-in-linear">
      <router-view></router-view>
    </transition>
    <footer-infor></footer-infor>
  </div>
</template>
<script>
import bigBanner from "../../components/bigBanner.vue";
import footerInfor from "../../components/footerInfor";
import inBanner from "../../components/interactive/inBanner.vue";
export default {
  components: {
    bigBanner,
    footerInfor,
    inBanner
  },
  data() {
    return {
      isDetail: false
    };
  },
  created() {
    this.isDetail = this.$route.path.split("/").pop() === "detail";
  },
  watch: {
    $route(to, from) {
      this.isDetail = to.path.split("/").pop() === "detail";
    }
  }
};
</script>
<style lang="less" scoped>
.in-top-banner {
  width: 100%;
  height: 400px;
  background: #f3f7fa;
  padding-top: 25px;
}
</style>



