<template>
  <div class="borde-list">
    <ul v-if="datas.length > 0">
      <li v-for="item in datas" :key="item.id">
        <router-link :to="item.path">{{item.text}}</router-link>
      </li>
    </ul>
    <div class="tips" v-if="datas.length == 0">暂无消息</div>
    <router-link v-if="datas.length > 0" class="more" to>
      更多
      <i class="iconfont icon-jiantou"></i>
    </router-link>
  </div>
</template>
<script>
export default {
  props: {
    datas: Array
  }
};
</script>
<style lang="less" scoped>
.borde-list {
  width: 100%;
  height: 280px;
  text-align: left;
  position: relative;
  color: #007bff;
  a {
    color: #2c3e50;
    font-size: 0.9rem;
  }
  .more {
    position: absolute;
    bottom: 0;
    right: 8px;
    color: #007bff;
  }
  .tips{
    width: 100%;
    text-align: center;
    color: #bebebe;
    margin-top: 25px;
  }
}
</style>



