<template>
  <div class="in-banner">
    <el-carousel class="in-carousel fl" height="350px" :class="{'in-detail': detail}">
      <el-carousel-item v-for="(item,index) in inList" :key="index" @click.native="toggleDetail">
        <div class="img-box">
          <img :src="item.src" :draggable="false">
        </div>
      </el-carousel-item>
    </el-carousel>
    <div v-if="!detail" class="tab-content fl">
      <el-tabs :stretch="true" class="in-tabs" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="公告" name="first">
          <borde-list :datas="notices"></borde-list>
        </el-tab-pane>
        <el-tab-pane label="资讯" name="second">
          <borde-list :datas="news"></borde-list>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div v-if="detail" class="detail-content fl">
      <div class="in-de-head">
        <span class="title fl">{{title}}</span>
        <span class="fr">课程评价：
          <el-rate
            class="fr"
            v-model="rate"
            disabled
            show-score
            text-color="#ff9900"
            score-template="{value}"
          ></el-rate>
        </span>
        <div class="clear"></div>
      </div>
      <div class="in-de-content">
        <div>
          <span>总课次：{{lessonCount}}(共{{lessonCount}}课时)</span>
          <span>主讲教师：{{teacher}}</span>
        </div>
        <div>
          <span>开课时间：{{dateTime}}</span>
          <span>报名时间：截止至{{deadLine}}</span>
        </div>
      </div>
      <div class="in-de-desc">{{desc}}</div>
      <el-button class="fl white-text blue-bg">
        <i class="iconfont icon-baoming"></i>&nbsp;
        课程报名
      </el-button>
    </div>
    <div class="clear"></div>
  </div>
</template>
<script>
import bordeList from "./bordList";
export default {
  props: {
    detail: {
      type: Boolean,
      default: false
    }
  },
  components: {
    bordeList
  },
  data() {
    return {
      inList: [
        {
          src: require("../../assets/in-banner.png"),
          path: ""
        }
      ],
      notices: [
        {
          id: "123",
          path: "",
          text: "期末将至，各教研组抓紧开展复习工作"
        }
      ],
      news: [],
      activeName: "first",
      title: "期末考冲刺提分课程",
      rate: 4.7,
      lessonCount: 495,
      teacher: "特级教师李金铭",
      dateTime: "2019-01-28 01:00",
      deadLine: "2019-01-01 12:00",
      desc:
        "课程简介：李金铭教你如何应付期末考，教你猜透出题老师的意图，教你如何跟监考老师斗地主，价值998的课程，现在免费听，998买不了吃亏买不了上当，跟着崔老师学斗地主，清华北大不是梦"
    };
  },
  methods: {
    handleClick() {},
    toggleDetail() {
      if (this.$route.path.split("/").pop() === "detail ") {
        this.$router.push("/main/interactive/page");
      }else {
        this.$router.push("/main/interactive/detail");
      }
    }
  }
};
</script>
<style lang="less" scoped>
.in-banner {
  width: 1200px;
  height: 350px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 8px;
  background: #fff;
  overflow: hidden;
  transition: all 0.5s;
  position: relative;
  .in-carousel {
    width: 900px;
    height: 100%;
    transition: all 0.5s;
    cursor: pointer;
  }
  img {
    width: 900px;
    height: 350px;
  }
  .in-detail {
    width: 460px !important;
  }
  .tab-content {
    width: 300px;
    .in-tabs {
      width: 100%;
    }
  }
  .detail-content {
    width: 740px;
    padding: 20px 25px;
    .in-de-head {
      width: 100%;
      .title {
        font-size: 1.52rem;
        font-weight: 600;
      }
    }
    .in-de-content {
      width: 100%;
      margin-top: 25px;
      color: #959595;
      text-align: left;
      div {
        width: 555px;
        span {
          display: inline-block;
          width: 49%;
        }
      }
    }
    .in-de-desc {
      margin-top: 25px;
      padding: 15px;
      background: #f3f7fa;
      color: #959595;
      text-align: left;
      font-size: 0.9rem;
    }
    button {
      bottom: 12px;
      left: 485px;
      position: absolute;
      .iconfont {
        font-size: 1.52rem;
        vertical-align: middle;
      }
      font-size: 1.1rem;
    }
  }
}
</style>



